import React, { useState, useEffect } from 'react';
import axios from 'axios';

// Datos de ejemplo para modo fallback
const mockData = {
  attempts: [
    {
      _id: "mock1",
      studentName: "María García",
      studentAvatar: "",
      quizId: "Mecánica de Fluidos Básico",
      score: 8,
      totalQuizzes: 10,
      timeTaken: 320,
      isActive: false,
      startTime: new Date(Date.now() - 3600000).toISOString()
    },
    {
      _id: "mock2",
      studentName: "Juan Pérez",
      studentAvatar: "",
      quizId: "Mecánica de Fluidos Avanzado",
      score: 9,
      totalQuizzes: 10,
      timeTaken: 450,
      isActive: false,
      startTime: new Date(Date.now() - 7200000).toISOString()
    },
    {
      _id: "mock3",
      studentName: "Ana Martínez",
      studentAvatar: "",
      quizId: "Mecánica de Fluidos Básico",
      score: 6,
      totalQuizzes: 10,
      timeTaken: 280,
      isActive: false,
      startTime: new Date(Date.now() - 10800000).toISOString()
    },
    {
      _id: "mock4",
      studentName: "Carlos López",
      studentAvatar: "",
      quizId: "Mecánica de Fluidos Avanzado",
      score: 7,
      totalQuizzes: 10,
      timeTaken: 510,
      isActive: true,
      startTime: new Date(Date.now() - 900000).toISOString()
    }
  ],
  stats: {
    totalAttempts: 4,
    completedAttempts: 3,
    scoreStats: {
      avgScore: 0.75,
      maxScore: 0.9,
      minScore: 0.6
    },
    topScorers: [
      {
        studentName: "Juan Pérez",
        studentAvatar: "",
        score: 9,
        totalQuizzes: 10,
        timeTaken: 450
      },
      {
        studentName: "María García",
        studentAvatar: "",
        score: 8,
        totalQuizzes: 10,
        timeTaken: 320
      },
      {
        studentName: "Carlos López",
        studentAvatar: "",
        score: 7,
        totalQuizzes: 10,
        timeTaken: 510
      }
    ]
  },
  activeUsers: [
    {
      _id: "mock4",
      studentName: "Carlos López",
      startTime: new Date(Date.now() - 900000).toISOString(),
      score: 5
    }
  ]
};

const Dashboard = () => {
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [attempts, setAttempts] = useState([]);
  const [stats, setStats] = useState(null);
  const [activeUsers, setActiveUsers] = useState([]);
  const [tab, setTab] = useState('attempts'); // 'attempts', 'stats', 'active'
  const [useMockData, setUseMockData] = useState(false);

  const apiBase = process.env.REACT_APP_API_URL || 'https://simulador-fluidos-backend-0ce217669297.herokuapp.com';

  useEffect(() => {
    const fetchData = async () => {
      try {
        setLoading(true);
        
        // Cargar todos los datos necesarios
        const [attemptsRes, statsRes, activeRes] = await Promise.all([
          axios.get(`${apiBase}/api/quiz-attempts`),
          axios.get(`${apiBase}/api/quiz-stats`),
          axios.get(`${apiBase}/api/quiz-attempts/active`)
        ]);
        
        setAttempts(attemptsRes.data.data || []);
        setStats(statsRes.data.data || null);
        setActiveUsers(activeRes.data.data || []);
        setError(null);
        setUseMockData(false);
      } catch (err) {
        console.error("Error al cargar datos:", err);
        setError("No se pudieron cargar los datos. Verifica la conexión con el servidor.");
      } finally {
        setLoading(false);
      }
    };

    fetchData();
    
    // Actualizar usuarios activos cada 30 segundos
    const interval = setInterval(async () => {
      if (!useMockData) {
        try {
          const res = await axios.get(`${apiBase}/api/quiz-attempts/active`);
          setActiveUsers(res.data.data || []);
        } catch (error) {
          console.error("Error al actualizar usuarios activos:", error);
        }
      }
    }, 30000);

    return () => clearInterval(interval);
  }, [apiBase, useMockData]);

  // Formatear fecha y hora
  const formatDateTime = (dateString) => {
    const date = new Date(dateString);
    return new Intl.DateTimeFormat('es-ES', {
      year: 'numeric', month: 'short', day: 'numeric',
      hour: '2-digit', minute: '2-digit', second: '2-digit'
    }).format(date);
  };

  // Formatear tiempo en minutos y segundos
  const formatTime = (seconds) => {
    if (!seconds) return "N/A";
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins}m ${secs}s`;
  };

  // Calcular porcentaje
  const calculatePercentage = (score, total) => {
    if (!total) return 0;
    return Math.round((score / total) * 100);
  };

  // Cargar datos de ejemplo
  const loadMockData = () => {
    setAttempts(mockData.attempts);
    setStats(mockData.stats);
    setActiveUsers(mockData.activeUsers);
    setUseMockData(true);
    setError(null);
  };

  // Renderizar contenido según la pestaña seleccionada
  const renderContent = () => {
    if (loading) {
      return (
        <div className="flex justify-center items-center h-64">
          <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-blue-500"></div>
        </div>
      );
    }

    if (error) {
      return (
        <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded">
          <p>{error}</p>
          <div className="flex space-x-3 mt-3">
            <button 
              onClick={() => window.location.reload()} 
              className="bg-red-500 hover:bg-red-700 text-white font-bold py-1 px-2 rounded"
            >
              Reintentar
            </button>
            <button 
              onClick={loadMockData} 
              className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-1 px-2 rounded"
            >
              Mostrar datos de ejemplo
            </button>
          </div>
        </div>
      );
    }

    switch (tab) {
      case 'attempts':
        return renderAttempts();
      case 'stats':
        return renderStats();
      case 'active':
        return renderActiveUsers();
      default:
        return renderAttempts();
    }
  };

  // Renderizar intentos de pruebas
  const renderAttempts = () => {
    if (attempts.length === 0) {
      return <p className="text-center py-4">No hay intentos registrados.</p>;
    }

    return (
      <div className="overflow-x-auto">
        <table className="min-w-full bg-white border border-gray-200">
          <thead className="bg-gray-100">
            <tr>
              <th className="py-2 px-4 border-b text-left">Estudiante</th>
              <th className="py-2 px-4 border-b text-left">Cuestionario</th>
              <th className="py-2 px-4 border-b text-center">Puntuación</th>
              <th className="py-2 px-4 border-b text-center">Tiempo</th>
              <th className="py-2 px-4 border-b text-center">Completado</th>
              <th className="py-2 px-4 border-b text-center">Fecha</th>
            </tr>
          </thead>
          <tbody>
            {attempts.map((attempt) => (
              <tr key={attempt._id} className="hover:bg-gray-50">
                <td className="py-2 px-4 border-b">
                  <div className="flex items-center">
                    <div className="w-8 h-8 mr-2 bg-gray-200 rounded-full flex items-center justify-center overflow-hidden">
                      {attempt.studentAvatar ? (
                        <img src={attempt.studentAvatar} alt={attempt.studentName} className="w-full h-full object-cover" />
                      ) : (
                        <span className="text-sm">{attempt.studentName.charAt(0)}</span>
                      )}
                    </div>
                    <span>{attempt.studentName}</span>
                  </div>
                </td>
                <td className="py-2 px-4 border-b">{attempt.quizId}</td>
                <td className="py-2 px-4 border-b text-center">
                  <div className="inline-block bg-blue-100 text-blue-800 py-1 px-2 rounded-full text-xs font-semibold">
                    {attempt.score}/{attempt.totalQuizzes} ({calculatePercentage(attempt.score, attempt.totalQuizzes)}%)
                  </div>
                </td>
                <td className="py-2 px-4 border-b text-center">{formatTime(attempt.timeTaken)}</td>
                <td className="py-2 px-4 border-b text-center">
                  {attempt.isActive ? (
                    <span className="inline-block bg-yellow-100 text-yellow-800 py-1 px-2 rounded text-xs">En progreso</span>
                  ) : (
                    <span className="inline-block bg-green-100 text-green-800 py-1 px-2 rounded text-xs">Completado</span>
                  )}
                </td>
                <td className="py-2 px-4 border-b text-sm">{formatDateTime(attempt.startTime)}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    );
  };

  // Renderizar estadísticas
  const renderStats = () => {
    if (!stats) {
      return <p className="text-center py-4">No hay estadísticas disponibles.</p>;
    }

    return (
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4 mb-6">
        <div className="bg-white p-4 rounded-lg shadow">
          <h3 className="text-lg font-semibold text-gray-700 mb-2">Resumen</h3>
          <div className="flex justify-between mb-2">
            <span className="text-gray-600">Total de intentos:</span>
            <span className="font-medium">{stats.totalAttempts}</span>
          </div>
          <div className="flex justify-between mb-2">
            <span className="text-gray-600">Intentos completados:</span>
            <span className="font-medium">{stats.completedAttempts}</span>
          </div>
          <div className="flex justify-between">
            <span className="text-gray-600">Tasa de finalización:</span>
            <span className="font-medium">
              {stats.totalAttempts ? Math.round((stats.completedAttempts / stats.totalAttempts) * 100) : 0}%
            </span>
          </div>
        </div>

        <div className="bg-white p-4 rounded-lg shadow">
          <h3 className="text-lg font-semibold text-gray-700 mb-2">Puntuaciones</h3>
          {stats.scoreStats ? (
            <>
              <div className="flex justify-between mb-2">
                <span className="text-gray-600">Puntuación media:</span>
                <span className="font-medium">{Math.round(stats.scoreStats.avgScore * 100)}%</span>
              </div>
              <div className="flex justify-between mb-2">
                <span className="text-gray-600">Puntuación máxima:</span>
                <span className="font-medium">{Math.round(stats.scoreStats.maxScore * 100)}%</span>
              </div>
              <div className="flex justify-between">
                <span className="text-gray-600">Puntuación mínima:</span>
                <span className="font-medium">{Math.round(stats.scoreStats.minScore * 100)}%</span>
              </div>
            </>
          ) : (
            <p>No hay datos de puntuación disponibles.</p>
          )}
        </div>

        <div className="bg-white p-4 rounded-lg shadow md:col-span-2 lg:col-span-1">
          <h3 className="text-lg font-semibold text-gray-700 mb-2">Mejores puntuaciones</h3>
          {stats.topScorers && stats.topScorers.length > 0 ? (
            <div className="max-h-60 overflow-y-auto">
              {stats.topScorers.map((scorer, index) => (
                <div key={index} className="flex items-center justify-between py-2 border-b last:border-b-0">
                  <div className="flex items-center">
                    <div className="w-8 h-8 mr-2 bg-gray-200 rounded-full flex items-center justify-center overflow-hidden">
                      {scorer.studentAvatar ? (
                        <img src={scorer.studentAvatar} alt={scorer.studentName} className="w-full h-full object-cover" />
                      ) : (
                        <span className="text-sm">{scorer.studentName.charAt(0)}</span>
                      )}
                    </div>
                    <span>{scorer.studentName}</span>
                  </div>
                  <div className="text-right">
                    <div className="font-semibold">{calculatePercentage(scorer.score, scorer.totalQuizzes)}%</div>
                    <div className="text-xs text-gray-500">{formatTime(scorer.timeTaken)}</div>
                  </div>
                </div>
              ))}
            </div>
          ) : (
            <p>No hay datos de mejores puntuaciones.</p>
          )}
        </div>
      </div>
    );
  };

  // Renderizar usuarios activos
  const renderActiveUsers = () => {
    if (activeUsers.length === 0) {
      return <p className="text-center py-4">No hay usuarios activos en este momento.</p>;
    }

    return (
      <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
        {activeUsers.map((user) => (
          <div key={user._id} className="bg-white p-4 rounded-lg shadow">
            <div className="flex items-center mb-2">
              <div className="w-10 h-10 mr-3 bg-gray-200 rounded-full flex items-center justify-center overflow-hidden">
                {user.studentAvatar ? (
                  <img src={user.studentAvatar} alt={user.studentName} className="w-full h-full object-cover" />
                ) : (
                  <span>{user.studentName.charAt(0)}</span>
                )}
              </div>
              <div>
                <h3 className="font-semibold">{user.studentName}</h3>
                <p className="text-xs text-gray-500">
                  Comenzó hace {Math.round((Date.now() - new Date(user.startTime)) / 60000)} minutos
                </p>
              </div>
            </div>
            <div className="flex justify-between text-sm">
              <span>Puntuación actual:</span>
              <span className="font-semibold">{user.score || 0} pts</span>
            </div>
          </div>
        ))}
      </div>
    );
  };

  return (
    <div className="container mx-auto px-4 py-6">
      <h1 className="text-2xl font-bold mb-6 text-gray-800">Panel de Administración</h1>
      
      {/* Pestañas de navegación */}
      <div className="flex border-b mb-6">
        <button 
          onClick={() => setTab('attempts')} 
          className={`px-4 py-2 ${tab === 'attempts' ? 'border-b-2 border-blue-500 text-blue-600' : 'text-gray-500'}`}
        >
          Todos los intentos
        </button>
        <button 
          onClick={() => setTab('stats')} 
          className={`px-4 py-2 ${tab === 'stats' ? 'border-b-2 border-blue-500 text-blue-600' : 'text-gray-500'}`}
        >
          Estadísticas
        </button>
        <button 
          onClick={() => setTab('active')} 
          className={`px-4 py-2 ${tab === 'active' ? 'border-b-2 border-blue-500 text-blue-600' : 'text-gray-500'} flex items-center`}
        >
          En tiempo real
          {activeUsers.length > 0 && (
            <span className="ml-2 bg-green-500 text-white text-xs rounded-full w-5 h-5 flex items-center justify-center">
              {activeUsers.length}
            </span>
          )}
        </button>
      </div>
      
      {/* Contenido principal */}
      {renderContent()}
    </div>
  );
};

export default Dashboard; 