import React, { useState, useEffect } from "react";

//---------------------------------------------
// Helper components
//---------------------------------------------
function NumberField({ label, value, setValue, unit }) {
  return (
    <div className="flex flex-col gap-1 mb-3">
      <label className="text-sm font-semibold">{label}</label>
      <div className="flex items-center gap-2">
        <input
          type="number"
          step="any"
          value={value}
          onChange={(e) => setValue(parseFloat(e.target.value) || 0)}
          className="w-32 p-2 border rounded"
        />
        {unit && <span className="text-xs text-gray-500">{unit}</span>}
      </div>
    </div>
  );
}

function SliderField({ label, value, setValue, min, max, step, unit }) {
  return (
    <div className="flex flex-col gap-1 mb-3">
      <label className="text-sm font-semibold">{label}: {value} {unit}</label>
      <div className="flex items-center gap-2">
        <input
          type="range"
          min={min}
          max={max}
          step={step}
          value={value}
          onChange={(e) => setValue(parseFloat(e.target.value))}
          className="w-full"
        />
      </div>
    </div>
  );
}

//---------------------------------------------
// 1. Orifice Plate Simulator
//---------------------------------------------
function OrificePlateSimulator() {
  const [D, setD] = useState(0.04); // pipe dia [m]
  const [d, setd] = useState(0.02); // orifice dia [m]
  const [rho, setRho] = useState(998); // density [kg/m3]
  const [deltaP, setDeltaP] = useState(5000); // Pa
  const [Cd, setCd] = useState(0.62);
  const [isSimulating, setIsSimulating] = useState(false);
  const [animationProgress, setAnimationProgress] = useState(0);
  
  const beta = d / D;
  const A2 = (Math.PI * d ** 2) / 4;
  const Q =
    Cd *
    A2 *
    Math.sqrt((2 * deltaP) / (rho * (1 - beta ** 4)));
    
  // Animation effect
  useEffect(() => {
    if (isSimulating) {
      const interval = setInterval(() => {
        setAnimationProgress((prev) => {
          if (prev >= 100) return 0;
          return prev + 1;
        });
      }, 50);
      return () => clearInterval(interval);
    }
  }, [isSimulating]);

  const orificePlateVisualization = (
    <svg viewBox="0 0 300 150" className="w-full h-48 border rounded bg-blue-50">
      {/* Pipe */}
      <rect x="50" y="50" width="200" height={D * 1000} rx="2" fill="#aaa" />
      
      {/* Orifice plate */}
      <rect x="150" y="30" width="4" height="90" fill="#555" />
      
      {/* Orifice */}
      <circle cx="152" cy="75" r={d * 500} fill="#fff" />
      
      {/* Flow indicator (animated) */}
      {isSimulating && (
        <>
          <circle cx={50 + (animationProgress * 0.8)} cy="75" r="3" fill="blue" opacity="0.7" />
          <circle cx={50 + (animationProgress * 1.2)} cy="75" r="2" fill="blue" opacity="0.5" />
          <circle cx={50 + (animationProgress * 1.5)} cy="75" r="2.5" fill="blue" opacity="0.6" />
          
          {/* Faster flow after orifice */}
          {animationProgress > 60 && (
            <>
              <circle cx={152 + ((animationProgress - 60) * 2.5)} cy="75" r="2" fill="blue" opacity="0.8" />
              <circle cx={152 + ((animationProgress - 60) * 3)} cy="75" r="1.5" fill="blue" opacity="0.7" />
            </>
          )}
        </>
      )}
      
      {/* Pressure indicators */}
      <text x="80" y="30" fontSize="10" fill="#333">P₁: {deltaP.toFixed(0)} Pa</text>
      <text x="180" y="30" fontSize="10" fill="#333">P₂: {(deltaP * 0.7).toFixed(0)} Pa</text>
      
      {/* Flow rate */}
      <text x="150" y="130" textAnchor="middle" fontSize="12" fill="#333">
        Q = {Q.toFixed(4)} m³/s
      </text>
    </svg>
  );

  return (
    <div className="w-full max-w-xl bg-white rounded-lg shadow p-4">
      <div className="mb-4">
        <h3 className="text-xl font-semibold text-blue-800">Placa de Orificio</h3>
      </div>
      
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <div className="flex flex-col gap-4">
          <SliderField 
            label="Diámetro de tubería D" 
            value={D} 
            setValue={setD} 
            min={0.01} 
            max={0.1} 
            step={0.005} 
            unit="m" 
          />
          
          <SliderField 
            label="Diámetro de orificio d" 
            value={d} 
            setValue={setd} 
            min={0.005} 
            max={D * 0.95} 
            step={0.001} 
            unit="m" 
          />
          
          <NumberField 
            label="Densidad del fluido ρ" 
            value={rho} 
            setValue={setRho} 
            unit="kg/m³" 
          />
          
          <SliderField 
            label="Diferencia de presión ΔP" 
            value={deltaP} 
            setValue={setDeltaP} 
            min={1000} 
            max={10000} 
            step={100} 
            unit="Pa" 
          />
          
          <NumberField 
            label="Coeficiente de descarga Cd" 
            value={Cd} 
            setValue={setCd} 
          />
          
          <div className="text-lg font-semibold mt-4 text-blue-700">
            Q = {Q.toFixed(4)} m³/s ({(Q * 60).toFixed(2)} L/min)
          </div>
          
          <button
            className="mt-4 px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700 transition"
            onClick={() => setIsSimulating(!isSimulating)}
          >
            {isSimulating ? "Detener Simulación" : "Iniciar Simulación"}
          </button>
        </div>
        
        <div className="flex flex-col items-center justify-center">
          {orificePlateVisualization}
          <div className="mt-3 text-center text-sm text-gray-600">
            {isSimulating ? "Visualización del flujo a través de la placa de orificio" : "Presiona Iniciar Simulación para visualizar el flujo"}
          </div>
        </div>
      </div>
    </div>
  );
}

//---------------------------------------------
// 2. Rotameter Simulator
//---------------------------------------------
function RotameterSimulator() {
  const [rhoFloat, setRhoFloat] = useState(2600); // kg/m3
  const [Vf, setVf] = useState(0.8e-6); // m3 (0.8 mL)
  const [rhoFluid, setRhoFluid] = useState(998);
  const [Cd, setCd] = useState(0.8);
  const [At, setAt] = useState(1.1e-4); // m2 (1.1 cm2)
  const [isSimulating, setIsSimulating] = useState(false);
  const [floatHeight, setFloatHeight] = useState(30); // visual position of float

  const g = 9.81;
  const V = Math.sqrt(((rhoFloat - rhoFluid) * Vf * g * 2) / (Cd * rhoFluid * At));
  const Q = V * At; // m3/s
  
  // Calculate normalized float height based on flow rate
  useEffect(() => {
    // Map Q to height position (0-80)
    const maxQ = 0.0001; // arbitrary reference for max expected flow
    const minHeight = 10;
    const maxHeight = 80;
    const height = minHeight + (Q / maxQ) * (maxHeight - minHeight);
    setFloatHeight(Math.min(Math.max(height, minHeight), maxHeight));
  }, [Q]);
  
  // Animation effect
  useEffect(() => {
    if (isSimulating) {
      // Animation logic
      const interval = setInterval(() => {
        // Animate bubbles or particles
      }, 50);
      return () => clearInterval(interval);
    }
  }, [isSimulating]);

  const rotameterVisualization = (
    <svg viewBox="0 0 100 200" className="w-full h-64 border rounded bg-blue-50">
      {/* Tube */}
      <rect x="40" y="20" width="20" height="160" rx="0" fill="rgba(255,255,255,0.7)" stroke="#888" />
      
      {/* Tapered tube internal shape */}
      <path d="M45,180 L55,180 L58,20 L42,20 Z" fill="rgba(255,255,255,0.3)" stroke="#ddd" />
      
      {/* Float */}
      <circle cx="50" cy={180 - floatHeight} r="8" fill="#666" stroke="#444" />
      
      {/* Flow measurements scale */}
      <line x1="65" y1="20" x2="65" y2="180" stroke="#333" strokeWidth="1" />
      {[0, 25, 50, 75, 100].map((percent, i) => (
        <React.Fragment key={i}>
          <line x1="65" y1={180 - percent * 1.6} x2="70" y2={180 - percent * 1.6} stroke="#333" strokeWidth="1" />
          <text x="72" y={180 - percent * 1.6 + 3} fontSize="6">{percent}%</text>
        </React.Fragment>
      ))}
      
      {/* Flow arrows (animated when simulating) */}
      {isSimulating && (
        <>
          <path d="M50,190 L50,185 L47,188 M50,185 L53,188" stroke="blue" fill="none" strokeWidth="0.5" />
          <path d="M50,10 L50,15 L47,12 M50,15 L53,12" stroke="blue" fill="none" strokeWidth="0.5" />
        </>
      )}
      
      {/* Flow rate display */}
      <text x="50" y="195" textAnchor="middle" fontSize="7" fill="#333">
        Q ≈ {Q.toFixed(6)} m³/s
      </text>
    </svg>
  );

  return (
    <div className="w-full max-w-xl bg-white rounded-lg shadow p-4">
      <div className="mb-4">
        <h3 className="text-xl font-semibold text-blue-800">Rotámetro</h3>
      </div>
      
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <div className="flex flex-col gap-4">
          <SliderField 
            label="Densidad del flotador ρ" 
            value={rhoFloat} 
            setValue={setRhoFloat} 
            min={1500} 
            max={5000} 
            step={100} 
            unit="kg/m³" 
          />
          
          <SliderField 
            label="Volumen del flotador" 
            value={Vf * 1e6} 
            setValue={(val) => setVf(val * 1e-6)} 
            min={0.1} 
            max={2} 
            step={0.1} 
            unit="mL" 
          />
          
          <SliderField 
            label="Densidad del fluido ρ" 
            value={rhoFluid} 
            setValue={setRhoFluid} 
            min={800} 
            max={1200} 
            step={10} 
            unit="kg/m³" 
          />
          
          <NumberField 
            label="Coeficiente de descarga Cd" 
            value={Cd} 
            setValue={setCd} 
          />
          
          <NumberField 
            label="Área del tubo At" 
            value={At * 1e4} 
            setValue={(val) => setAt(val * 1e-4)} 
            unit="cm²" 
          />
          
          <div className="text-lg font-semibold mt-4 text-blue-700">
            Q ≈ {Q.toFixed(6)} m³/s ({(Q * 3600 * 1000).toFixed(2)} L/h)
          </div>
          
          <button
            className="mt-4 px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700 transition"
            onClick={() => setIsSimulating(!isSimulating)}
          >
            {isSimulating ? "Detener Simulación" : "Iniciar Simulación"}
          </button>
        </div>
        
        <div className="flex flex-col items-center justify-center">
          {rotameterVisualization}
          <div className="mt-3 text-center text-sm text-gray-600">
            {isSimulating ? "Visualización del flotador en el rotámetro" : "Presiona Iniciar Simulación para visualizar el flujo"}
          </div>
        </div>
      </div>
    </div>
  );
}

//---------------------------------------------
// 3. Centrifugal Pump Simulator
//---------------------------------------------
function CentrifugalPumpSimulator() {
  const [Q, setQ] = useState(0.0005); // m3/s
  const [H, setH] = useState(40); // m
  const [eta, setEta] = useState(0.6);
  const [isSimulating, setIsSimulating] = useState(false);
  const [rotationAngle, setRotationAngle] = useState(0);
  
  const rho = 1000;
  const g = 9.81;
  const P = (rho * g * Q * H) / eta;
  
  // Animation effect
  useEffect(() => {
    if (isSimulating) {
      const interval = setInterval(() => {
        setRotationAngle(prev => (prev + 5) % 360);
      }, 30);
      return () => clearInterval(interval);
    }
  }, [isSimulating]);

  const centrifugalPumpVisualization = (
    <svg viewBox="0 0 200 200" className="w-full h-64 border rounded bg-blue-50">
      {/* Pump casing */}
      <circle cx="100" cy="100" r="50" fill="#ddd" stroke="#999" />
      
      {/* Impeller */}
      <g transform={`rotate(${rotationAngle}, 100, 100)`}>
        <circle cx="100" cy="100" r="10" fill="#666" />
        <path d="M100,100 L130,80 L135,95 Z" fill="#888" />
        <path d="M100,100 L70,80 L65,95 Z" fill="#888" />
        <path d="M100,100 L120,130 L100,140 Z" fill="#888" />
        <path d="M100,100 L80,130 L100,140 Z" fill="#888" />
      </g>
      
      {/* Inlet */}
      <rect x="10" y="95" width="40" height="10" fill="#aaa" />
      
      {/* Outlet */}
      <rect x="100" y="10" width="10" height="40" fill="#aaa" />
      
      {/* Flow arrows (animated when simulating) */}
      {isSimulating && (
        <>
          {/* Inlet flow */}
          <circle cx={25 + (rotationAngle % 40)} cy="100" r="3" fill="blue" opacity="0.6" />
          <circle cx={15 + ((rotationAngle + 20) % 40)} cy="100" r="2.5" fill="blue" opacity="0.5" />
          
          {/* Outlet flow */}
          <circle cx="105" cy={30 - (rotationAngle % 20)} r="2" fill="blue" opacity="0.6" />
          <circle cx="105" cy={20 - ((rotationAngle + 10) % 20)} r="1.8" fill="blue" opacity="0.5" />
        </>
      )}
      
      {/* Parameters */}
      <text x="100" y="180" textAnchor="middle" fontSize="10" fill="#333">
        H = {H} m, η = {eta}
      </text>
      
      {/* Power */}
      <text x="100" y="190" textAnchor="middle" fontSize="10" fill="#333">
        P = {(P / 1000).toFixed(2)} kW
      </text>
    </svg>
  );

  return (
    <div className="w-full max-w-xl bg-white rounded-lg shadow p-4">
      <div className="mb-4">
        <h3 className="text-xl font-semibold text-blue-800">Bomba Centrífuga</h3>
      </div>
      
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <div className="flex flex-col gap-4">
          <SliderField 
            label="Caudal Q" 
            value={Q} 
            setValue={setQ} 
            min={0.0001} 
            max={0.001} 
            step={0.0001} 
            unit="m³/s" 
          />
          
          <SliderField 
            label="Altura H" 
            value={H} 
            setValue={setH} 
            min={10} 
            max={100} 
            step={5} 
            unit="m" 
          />
          
          <SliderField 
            label="Eficiencia η" 
            value={eta} 
            setValue={setEta} 
            min={0.3} 
            max={0.9} 
            step={0.05} 
            unit="" 
          />
          
          <div className="text-lg font-semibold mt-4 text-blue-700">
            P = {(P / 1000).toFixed(2)} kW
          </div>
          
          <div className="text-sm mt-1 text-gray-600">
            Equivale a {((P/1000) * 1.34102).toFixed(2)} HP
          </div>
          
          <button
            className="mt-4 px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700 transition"
            onClick={() => setIsSimulating(!isSimulating)}
          >
            {isSimulating ? "Detener Simulación" : "Iniciar Simulación"}
          </button>
        </div>
        
        <div className="flex flex-col items-center justify-center">
          {centrifugalPumpVisualization}
          <div className="mt-3 text-center text-sm text-gray-600">
            {isSimulating ? "Visualización del funcionamiento de la bomba centrífuga" : "Presiona Iniciar Simulación para visualizar la bomba"}
          </div>
        </div>
      </div>
    </div>
  );
}

//---------------------------------------------
// 4. Positive Displacement Pump Simulator
//---------------------------------------------
function PDPumpSimulator() {
  const [Vd, setVd] = useState(0.75e-6); // m3 per rev (0.75 mL)
  const [rpm, setRpm] = useState(30);
  const [slip, setSlip] = useState(1); // percent
  const [isSimulating, setIsSimulating] = useState(false);
  const [pistonPosition, setPistonPosition] = useState(0);
  
  const Q = Vd * rpm / 60 * (1 - slip / 100);
  
  // Animation effect
  useEffect(() => {
    if (isSimulating) {
      const interval = setInterval(() => {
        setPistonPosition(prev => (prev + 3) % 100);
      }, 30);
      return () => clearInterval(interval);
    }
  }, [isSimulating]);

  const pdPumpVisualization = (
    <svg viewBox="0 0 240 160" className="w-full h-64 border rounded bg-blue-50">
      {/* Cylinder */}
      <rect x="40" y="60" width="120" height="40" fill="#ddd" stroke="#999" />
      
      {/* Piston */}
      <rect 
        x={40 + pistonPosition} 
        y="65" 
        width="20" 
        height="30" 
        fill="#777" 
        stroke="#555" 
      />
      
      {/* Inlet valve */}
      <circle cx="60" cy="40" r="10" fill={pistonPosition < 40 ? "lightgreen" : "#ccc"} stroke="#999" />
      <line x1="60" y1="35" x2="60" y2="45" stroke={pistonPosition < 40 ? "green" : "#999"} strokeWidth="2" />
      <line x1="55" y1="40" x2="65" y2="40" stroke={pistonPosition < 40 ? "green" : "#999"} strokeWidth="2" />
      
      {/* Outlet valve */}
      <circle cx="140" cy="40" r="10" fill={pistonPosition > 50 ? "lightgreen" : "#ccc"} stroke="#999" />
      <line x1="140" y1="35" x2="140" y2="45" stroke="#999" strokeWidth="2" />
      <line x1="135" y1="40" x2="145" y2="40" stroke={pistonPosition > 50 ? "green" : "#999"} strokeWidth="2" />
      
      {/* Inlet/outlet pipes */}
      <rect x="30" y="30" width="10" height="30" fill="#aaa" />
      <rect x="160" y="30" width="10" height="30" fill="#aaa" />
      
      {/* Flow arrows */}
      {isSimulating && (
        <>
          {/* Inlet flow */}
          {pistonPosition < 40 && (
            <>
              <circle cx="35" cy={40 - (pistonPosition % 20)} r="3" fill="blue" opacity="0.6" />
              <circle cx="35" cy={30 - ((pistonPosition + 10) % 20)} r="2" fill="blue" opacity="0.5" />
            </>
          )}
          
          {/* Outlet flow */}
          {pistonPosition > 50 && (
            <>
              <circle cx="165" cy={40 - ((pistonPosition - 50) % 20)} r="3" fill="blue" opacity="0.6" />
              <circle cx="165" cy={30 - ((pistonPosition - 40) % 20)} r="2" fill="blue" opacity="0.5" />
            </>
          )}
        </>
      )}
      
      {/* Parameters display */}
      <text x="100" y="120" textAnchor="middle" fontSize="8" fill="#333">
        Vd = {(Vd * 1e6).toFixed(2)} mL/rev, rpm = {rpm}, slip = {slip}%
      </text>
      
      {/* Flow rate */}
      <text x="100" y="135" textAnchor="middle" fontSize="10" fill="#333">
        Q = {Q.toFixed(6)} m³/s ({(Q * 3600).toFixed(3)} m³/h)
      </text>
    </svg>
  );

  return (
    <div className="w-full max-w-xl bg-white rounded-lg shadow p-4">
      <div className="mb-4">
        <h3 className="text-xl font-semibold text-blue-800">Bomba de Desplazamiento Positivo</h3>
      </div>
      
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <div className="flex flex-col gap-4">
          <SliderField 
            label="Desplazamiento Vd" 
            value={Vd * 1e6} 
            setValue={(val) => setVd(val * 1e-6)} 
            min={0.1} 
            max={2} 
            step={0.05} 
            unit="mL/rev" 
          />
          
          <SliderField 
            label="Velocidad" 
            value={rpm} 
            setValue={setRpm} 
            min={5} 
            max={100} 
            step={5} 
            unit="rpm" 
          />
          
          <SliderField 
            label="Deslizamiento" 
            value={slip} 
            setValue={setSlip} 
            min={0} 
            max={10} 
            step={0.5} 
            unit="%" 
          />
          
          <div className="text-lg font-semibold mt-4 text-blue-700">
            Q = {Q.toFixed(6)} m³/s ({(Q * 3600 * 1000).toFixed(2)} L/h)
          </div>
          
          <button
            className="mt-4 px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700 transition"
            onClick={() => setIsSimulating(!isSimulating)}
          >
            {isSimulating ? "Detener Simulación" : "Iniciar Simulación"}
          </button>
        </div>
        
        <div className="flex flex-col items-center justify-center">
          {pdPumpVisualization}
          <div className="mt-3 text-center text-sm text-gray-600">
            {isSimulating ? "Visualización del funcionamiento de la bomba de desplazamiento positivo" : "Presiona Iniciar Simulación para visualizar la bomba"}
          </div>
        </div>
      </div>
    </div>
  );
}

//---------------------------------------------
// Main component that encapsulates all simulators with tabs
//---------------------------------------------
function BombasPlacasRotametroSimulacion() {
  const [activeTab, setActiveTab] = useState("orifice");

  return (
    <div className="container mx-auto p-4">
      <h2 className="text-xl font-semibold mb-4 text-blue-700">Simulador de Bombas, Placas y Rotámetro</h2>
      
      {/* Tabs navigation */}
      <div className="flex mb-4 bg-gray-100 rounded-lg overflow-hidden">
        <button 
          className={`px-4 py-3 font-medium transition-colors duration-200 ${activeTab === 'orifice' ? 'bg-blue-600 text-white' : 'text-gray-700 hover:bg-blue-50'}`}
          onClick={() => setActiveTab('orifice')}
        >
          Placa de Orificio
        </button>
        <button 
          className={`px-4 py-3 font-medium transition-colors duration-200 ${activeTab === 'rotameter' ? 'bg-blue-600 text-white' : 'text-gray-700 hover:bg-blue-50'}`}
          onClick={() => setActiveTab('rotameter')}
        >
          Rotámetro
        </button>
        <button 
          className={`px-4 py-3 font-medium transition-colors duration-200 ${activeTab === 'centrifugal' ? 'bg-blue-600 text-white' : 'text-gray-700 hover:bg-blue-50'}`}
          onClick={() => setActiveTab('centrifugal')}
        >
          Bomba Centrífuga
        </button>
        <button 
          className={`px-4 py-3 font-medium transition-colors duration-200 ${activeTab === 'pd' ? 'bg-blue-600 text-white' : 'text-gray-700 hover:bg-blue-50'}`}
          onClick={() => setActiveTab('pd')}
        >
          Bomba de Desplazamiento
        </button>
      </div>

      {/* Content for each tab */}
      <div className="flex justify-center">
        {activeTab === 'orifice' && <OrificePlateSimulator />}
        {activeTab === 'rotameter' && <RotameterSimulator />}
        {activeTab === 'centrifugal' && <CentrifugalPumpSimulator />}
        {activeTab === 'pd' && <PDPumpSimulator />}
      </div>
    </div>
  );
}

export default BombasPlacasRotametroSimulacion;
