import React, { useState, useEffect } from 'react';

// URL base del backend
const API_BASE_URL = process.env.REACT_APP_API_URL || 'https://your-backend-url.com';

const QuizDashboard = () => {
  const [attempts, setAttempts] = useState([]);
  const [activeAttempts, setActiveAttempts] = useState([]);
  const [stats, setStats] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [tab, setTab] = useState('active');

  // Cargar datos iniciales
  useEffect(() => {
    const fetchData = async () => {
      try {
        setLoading(true);
        await Promise.all([
          fetchActiveAttempts(),
          fetchStats(),
          fetchAttempts()
        ]);
      } catch (err) {
        console.error('Error al cargar datos:', err);
        setError('Error al cargar los datos. Por favor, intenta de nuevo más tarde.');
      } finally {
        setLoading(false);
      }
    };

    fetchData();

    // Actualizar intentos activos cada 10 segundos
    const interval = setInterval(() => {
      fetchActiveAttempts();
    }, 10000);

    return () => clearInterval(interval);
  }, []);

  // Obtener intentos activos
  const fetchActiveAttempts = async () => {
    try {
      const response = await fetch(`${API_BASE_URL}/api/quiz-attempts/active`);
      if (!response.ok) throw new Error('Error al obtener intentos activos');
      
      const data = await response.json();
      setActiveAttempts(data.data || []);
    } catch (err) {
      console.error('Error al obtener intentos activos:', err);
    }
  };

  // Obtener estadísticas
  const fetchStats = async () => {
    try {
      const response = await fetch(`${API_BASE_URL}/api/quiz-stats`);
      if (!response.ok) throw new Error('Error al obtener estadísticas');
      
      const data = await response.json();
      setStats(data.data);
    } catch (err) {
      console.error('Error al obtener estadísticas:', err);
    }
  };

  // Obtener todos los intentos
  const fetchAttempts = async () => {
    try {
      const response = await fetch(`${API_BASE_URL}/api/quiz-attempts`);
      if (!response.ok) throw new Error('Error al obtener intentos');
      
      const data = await response.json();
      setAttempts(data.data || []);
    } catch (err) {
      console.error('Error al obtener intentos:', err);
    }
  };

  // Formatear tiempo
  const formatTime = (seconds) => {
    if (!seconds) return '00:00';
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`;
  };

  // Formatear fecha
  const formatDate = (dateString) => {
    if (!dateString) return '';
    const date = new Date(dateString);
    return date.toLocaleString();
  };

  // Calcular tiempo en juego
  const calculateActiveTime = (startTime) => {
    const start = new Date(startTime);
    const now = new Date();
    const diff = Math.floor((now - start) / 1000);
    return formatTime(diff);
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-blue-500"></div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="bg-red-100 border-l-4 border-red-500 text-red-700 p-4" role="alert">
        <p>{error}</p>
        <button 
          className="mt-2 px-4 py-2 bg-red-600 text-white rounded"
          onClick={() => window.location.reload()}
        >
          Reintentar
        </button>
      </div>
    );
  }

  return (
    <div className="bg-white rounded-lg shadow-md p-6">
      <h2 className="text-2xl font-bold text-blue-800 mb-4">Dashboard de Quiz - Profesor</h2>
      
      {/* Estadísticas generales */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-6">
        <div className="bg-blue-50 p-4 rounded-lg">
          <h3 className="text-lg font-medium text-blue-800 mb-2">Total de Intentos</h3>
          <p className="text-3xl font-bold">{stats?.totalAttempts || 0}</p>
        </div>
        
        <div className="bg-green-50 p-4 rounded-lg">
          <h3 className="text-lg font-medium text-green-800 mb-2">Completados</h3>
          <p className="text-3xl font-bold">{stats?.completedAttempts || 0}</p>
        </div>
        
        <div className="bg-purple-50 p-4 rounded-lg">
          <h3 className="text-lg font-medium text-purple-800 mb-2">Promedio de Aciertos</h3>
          <p className="text-3xl font-bold">{stats?.scoreStats?.avgScore ? 
            `${Math.round(stats.scoreStats.avgScore * 100)}%` : 
            '0%'}
          </p>
        </div>
      </div>
      
      {/* Navegación por pestañas */}
      <div className="border-b border-gray-200 mb-6">
        <nav className="flex space-x-4">
          <button
            className={`py-2 px-4 font-medium ${tab === 'active' ? 
              'border-b-2 border-blue-500 text-blue-600' : 
              'text-gray-500 hover:text-gray-700'}`}
            onClick={() => setTab('active')}
          >
            Participantes Activos ({activeAttempts.length})
          </button>
          <button
            className={`py-2 px-4 font-medium ${tab === 'top' ? 
              'border-b-2 border-blue-500 text-blue-600' : 
              'text-gray-500 hover:text-gray-700'}`}
            onClick={() => setTab('top')}
          >
            Top 10
          </button>
          <button
            className={`py-2 px-4 font-medium ${tab === 'all' ? 
              'border-b-2 border-blue-500 text-blue-600' : 
              'text-gray-500 hover:text-gray-700'}`}
            onClick={() => setTab('all')}
          >
            Todos los Intentos
          </button>
        </nav>
      </div>
      
      {/* Contenido de pestañas */}
      {tab === 'active' && (
        <div>
          <h3 className="text-lg font-medium text-gray-800 mb-4">Participantes Activos</h3>
          
          {activeAttempts.length === 0 ? (
            <p className="text-gray-500 italic">No hay participantes activos en este momento</p>
          ) : (
            <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-5 gap-4">
              {activeAttempts.map((attempt) => (
                <div key={attempt._id} className="bg-blue-50 p-4 rounded-lg text-center">
                  <div className="text-4xl mb-2">{attempt.studentAvatar}</div>
                  <p className="font-medium truncate">{attempt.studentName}</p>
                  <p className="text-sm text-gray-600">
                    Tiempo: {calculateActiveTime(attempt.startTime)}
                  </p>
                  <p className="text-sm text-gray-600">
                    Puntuación: {attempt.score || 0}
                  </p>
                </div>
              ))}
            </div>
          )}
        </div>
      )}
      
      {tab === 'top' && (
        <div>
          <h3 className="text-lg font-medium text-gray-800 mb-4">Top 10 Mejores Puntuaciones</h3>
          
          {!stats?.topScorers || stats.topScorers.length === 0 ? (
            <p className="text-gray-500 italic">No hay participantes con puntuaciones aún</p>
          ) : (
            <div className="overflow-x-auto">
              <table className="min-w-full divide-y divide-gray-200">
                <thead className="bg-gray-50">
                  <tr>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Posición
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Estudiante
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Puntuación
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Porcentaje
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Tiempo
                    </th>
                  </tr>
                </thead>
                <tbody className="bg-white divide-y divide-gray-200">
                  {stats.topScorers.map((scorer, index) => (
                    <tr key={scorer._id} className={index < 3 ? 'bg-yellow-50' : ''}>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <div className="text-sm font-medium text-gray-900">
                          {index + 1}
                        </div>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <div className="flex items-center">
                          <div className="text-2xl mr-3">{scorer.studentAvatar}</div>
                          <div className="text-sm font-medium text-gray-900">
                            {scorer.studentName}
                          </div>
                        </div>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <div className="text-sm text-gray-900">
                          {scorer.score} / {scorer.totalQuizzes}
                        </div>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <div className="text-sm text-gray-900">
                          {Math.round((scorer.score / scorer.totalQuizzes) * 100)}%
                        </div>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <div className="text-sm text-gray-900">
                          {formatTime(scorer.timeTaken)}
                        </div>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          )}
        </div>
      )}
      
      {tab === 'all' && (
        <div>
          <h3 className="text-lg font-medium text-gray-800 mb-4">Todos los Intentos</h3>
          
          {attempts.length === 0 ? (
            <p className="text-gray-500 italic">No hay intentos registrados</p>
          ) : (
            <div className="overflow-x-auto">
              <table className="min-w-full divide-y divide-gray-200">
                <thead className="bg-gray-50">
                  <tr>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Estudiante
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Puntuación
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Estado
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Inicio
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Completado
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Tiempo
                    </th>
                  </tr>
                </thead>
                <tbody className="bg-white divide-y divide-gray-200">
                  {attempts.map((attempt) => (
                    <tr key={attempt._id}>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <div className="flex items-center">
                          <div className="text-2xl mr-3">{attempt.studentAvatar}</div>
                          <div className="text-sm font-medium text-gray-900">
                            {attempt.studentName}
                          </div>
                        </div>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <div className="text-sm text-gray-900">
                          {attempt.score} / {attempt.totalQuizzes}
                        </div>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <span className={`px-2 inline-flex text-xs leading-5 font-semibold rounded-full 
                          ${attempt.isActive ? 'bg-green-100 text-green-800' : 'bg-blue-100 text-blue-800'}`}>
                          {attempt.isActive ? 'En Progreso' : 'Completado'}
                        </span>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                        {formatDate(attempt.startTime)}
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                        {attempt.completedAt ? formatDate(attempt.completedAt) : '-'}
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                        {attempt.timeTaken ? formatTime(attempt.timeTaken) : '-'}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          )}
        </div>
      )}
      
      {/* Botón de actualización */}
      <div className="mt-6 text-right">
        <button
          className="px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600"
          onClick={async () => {
            setLoading(true);
            await Promise.all([
              fetchActiveAttempts(),
              fetchStats(),
              fetchAttempts()
            ]);
            setLoading(false);
          }}
        >
          Actualizar Datos
        </button>
      </div>
    </div>
  );
};

export default QuizDashboard; 