import React, { useState } from 'react';

const avatars = [
  '👨‍🎓', '👩‍🎓', '🧑‍🔬', '👨‍🔬', '👩‍🔬', '🧙‍♂️', '🧙‍♀️', '🦸‍♂️', '🦸‍♀️', '🦊', 
  '🐱', '🐶', '🐼', '🐯', '🦁', '🐮', '🐷', '🐵', '🐸', '🐙'
];

const QuizLogin = ({ onLogin }) => {
  const [studentName, setStudentName] = useState('');
  const [selectedAvatar, setSelectedAvatar] = useState(avatars[0]);
  const [error, setError] = useState('');

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!studentName.trim()) {
      setError('Por favor, ingresa tu nombre para continuar');
      return;
    }
    
    onLogin({
      name: studentName,
      avatar: selectedAvatar,
      startTime: new Date().toISOString()
    });
  };

  return (
    <div className="max-w-md mx-auto bg-white rounded-lg shadow-md p-6">
      <h2 className="text-xl font-bold text-blue-800 mb-4 text-center">
        Iniciar Quiz de Mecánica de Fluidos
      </h2>
      
      <form onSubmit={handleSubmit}>
        <div className="mb-4">
          <label htmlFor="studentName" className="block text-gray-700 mb-2">
            Ingresa tu nombre:
          </label>
          <input
            type="text"
            id="studentName"
            value={studentName}
            onChange={(e) => setStudentName(e.target.value)}
            className="w-full px-4 py-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
            placeholder="Nombre y Apellido"
          />
          {error && <p className="text-red-500 text-sm mt-1">{error}</p>}
        </div>
        
        <div className="mb-6">
          <label className="block text-gray-700 mb-2">
            Elige un avatar:
          </label>
          <div className="grid grid-cols-5 gap-2">
            {avatars.map((avatar, index) => (
              <button
                key={index}
                type="button"
                className={`text-2xl h-12 w-12 flex items-center justify-center rounded-full 
                  ${selectedAvatar === avatar ? 'bg-blue-100 ring-2 ring-blue-500' : 'bg-gray-100 hover:bg-gray-200'}`}
                onClick={() => setSelectedAvatar(avatar)}
              >
                {avatar}
              </button>
            ))}
          </div>
        </div>
        
        <button
          type="submit"
          className="w-full py-2 px-4 bg-blue-600 text-white font-medium rounded-md hover:bg-blue-700 transition-colors"
        >
          Comenzar Quiz
        </button>
      </form>
    </div>
  );
};

export default QuizLogin; 