// src/components/Tests/FluidMechanicsQuiz.jsx
import React, { useState, useRef, useEffect } from 'react';
import { DENSIDADES, GRAVEDAD } from '../../utils/constants';
import QuizLogin from './QuizLogin';

// URL base del backend
const API_BASE_URL = process.env.REACT_APP_API_URL || 'https://your-backend-url.com';

// Utilidad para determinar si el backend está disponible
const checkBackendAvailability = async () => {
  try {
    const response = await fetch(`${API_BASE_URL}/api/health-check`, { 
      method: 'GET',
      headers: { 'Content-Type': 'application/json' },
      // Timeout corto para no bloquear la interfaz
      signal: AbortSignal.timeout(3000)
    });
    return response.ok;
  } catch (error) {
    console.warn('Backend no disponible:', error);
    return false;
  }
};

// Función para guardar resultados localmente
const saveResultsLocally = (quizData) => {
  try {
    // Obtener resultados existentes
    const existingResults = JSON.parse(localStorage.getItem('quiz-results') || '[]');
    // Agregar nuevo resultado
    existingResults.push({
      ...quizData,
      savedAt: new Date().toISOString(),
      id: `local-${Date.now()}`
    });
    // Guardar resultados
    localStorage.setItem('quiz-results', JSON.stringify(existingResults));
    return true;
  } catch (error) {
    console.error('Error al guardar localmente:', error);
    return false;
  }
};

const FluidMechanicsQuiz = () => {
  // Estado para manejar el quiz actual
  const [currentQuiz, setCurrentQuiz] = useState(0);
  
  // Estado para manejar el progreso y la puntuación
  const [score, setScore] = useState(0);
  const [isCompleted, setIsCompleted] = useState(false);
  const [showFeedback, setShowFeedback] = useState(false);
  const [allCorrect, setAllCorrect] = useState(false);
  
  // Estado para manejar las respuestas arrastradas por el usuario
  const [userAnswers, setUserAnswers] = useState({});
  
  // Estado para manejar la información del estudiante y tiempo
  const [studentInfo, setStudentInfo] = useState(null);
  const [quizStarted, setQuizStarted] = useState(false);
  const [attemptId, setAttemptId] = useState(null);
  const [timeTaken, setTimeTaken] = useState(0);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [backendAvailable, setBackendAvailable] = useState(true);
  
  // Referencias a los elementos de destino
  const dropTargets = useRef([]);
  const timerRef = useRef(null);

  // Verificar disponibilidad del backend al iniciar
  useEffect(() => {
    const checkAvailability = async () => {
      const isAvailable = await checkBackendAvailability();
      setBackendAvailable(isAvailable);
      if (!isAvailable) {
        console.log('Funcionando en modo offline. Los resultados se guardarán localmente.');
      }
    };
    
    checkAvailability();
  }, []);

  // Efecto para iniciar el temporizador cuando comienza el quiz
  useEffect(() => {
    if (quizStarted && !isCompleted) {
      timerRef.current = setInterval(() => {
        setTimeTaken(prev => prev + 1);
      }, 1000);
    }
    
    return () => {
      if (timerRef.current) {
        clearInterval(timerRef.current);
      }
    };
  }, [quizStarted, isCompleted]);

  // Restablecer las respuestas del usuario cuando cambia el quiz
  useEffect(() => {
    setUserAnswers({});
    setShowFeedback(false);
    setAllCorrect(false);
    dropTargets.current = [];
  }, [currentQuiz]);

  // Manejar el login del estudiante
  const handleLogin = async (info) => {
    setStudentInfo(info);
    setQuizStarted(true);
    
    // Si el backend no está disponible, no intentar conectar
    if (!backendAvailable) {
      setError('El servidor no está disponible. Tus resultados se guardarán localmente.');
      return;
    }
    
    try {
      setLoading(true);
      // Iniciar un nuevo intento de quiz en el backend
      const response = await fetch(`${API_BASE_URL}/api/quiz-attempts`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          studentName: info.name,
          studentAvatar: info.avatar,
          startTime: info.startTime,
          quizId: 'fluid-mechanics-quiz'
        }),
      });
      
      if (!response.ok) {
        throw new Error('No se pudo iniciar el intento de quiz');
      }
      
      const data = await response.json();
      setAttemptId(data.attemptId);
      
    } catch (err) {
      console.error('Error al iniciar el quiz:', err);
      setError('Hubo un problema al conectar con el servidor. Tus resultados se guardarán localmente.');
      setBackendAvailable(false);
    } finally {
      setLoading(false);
    }
  };

  // Función para enviar los resultados al backend
  const sendResults = async () => {
    const quizData = {
      studentName: studentInfo.name,
      studentAvatar: studentInfo.avatar,
      score,
      totalQuizzes: quizzes.length,
      timeTaken,
      completedAt: new Date().toISOString(),
      startTime: studentInfo.startTime,
      quizId: 'fluid-mechanics-quiz'
    };
    
    // Si el backend no está disponible, guardar localmente
    if (!backendAvailable) {
      const saved = saveResultsLocally(quizData);
      if (!saved) {
        console.error('Error al guardar resultados localmente');
      }
      return;
    }
    
    // Si tenemos un ID de intento, intentar guardarlo en el backend
    if (!attemptId && !error) return;
    
    try {
      setLoading(true);
      
      const endpoint = error 
        ? `${API_BASE_URL}/api/quiz-results` 
        : `${API_BASE_URL}/api/quiz-attempts/${attemptId}/complete`;
      
      const response = await fetch(endpoint, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(quizData),
      });
      
      if (!response.ok) {
        throw new Error('No se pudieron enviar los resultados');
      }
      
    } catch (err) {
      console.error('Error al enviar resultados:', err);
      // Guardar localmente como respaldo
      saveResultsLocally(quizData);
    } finally {
      setLoading(false);
    }
  };

  // Lista de quizzes disponibles
  const quizzes = [
    {
      id: 'fluidos-newtonianos',
      title: 'Fluidos Newtonianos vs. No Newtonianos',
      description: 'Coloca los términos en los espacios correctos para completar las definiciones de fluidos newtonianos y no newtonianos.',
      text: `Un fluido newtoniano es aquel cuya [1] permanece constante independientemente de la [2] aplicada. La ecuación que lo define es τ = [3] · γ, donde τ es el [4] y γ es la [5].
      
En cambio, un fluido no newtoniano sigue la ley potencia τ = [6] · γ^n, donde n es el [7] y K es el [8]. Si n < 1, se trata de un fluido [9], mientras que si n > 1, es un fluido [10].`,
      options: [
        'viscosidad', 'tasa de deformación', 'µ', 'esfuerzo cortante', 'gradiente de velocidad',
        'K', 'índice de flujo', 'índice de consistencia', 'pseudoplástico', 'dilatante'
      ],
      answers: {
        1: 'viscosidad',
        2: 'tasa de deformación',
        3: 'µ',
        4: 'esfuerzo cortante',
        5: 'gradiente de velocidad',
        6: 'K',
        7: 'índice de flujo',
        8: 'índice de consistencia',
        9: 'pseudoplástico',
        10: 'dilatante'
      },
      dropTargets: 10
    },
    {
      id: 'ecuacion-bernoulli',
      title: 'Ecuación de Bernoulli y sus Aplicaciones',
      description: 'Completa la ecuación de Bernoulli arrastrando las variables y términos correctos.',
      text: `La ecuación de Bernoulli establece la conservación de energía en un fluido:

[1] / (ρg) + [2]² / (2g) + [3] = [4] / (ρg) + [5]² / (2g) + [6] + [7]

Donde:
- P es la [8]
- ρ es la [9]
- g es la [10]
- V es la [11]
- z es la [12]
- hf son las [13]`,
      options: [
        'P₁', 'V₁', 'z₁', 'P₂', 'V₂', 'z₂', 'hf', 
        'presión', 'densidad', 'aceleración de la gravedad', 'velocidad del fluido', 'altura sobre nivel referencia', 'pérdidas por fricción'
      ],
      answers: {
        1: 'P₁',
        2: 'V₁',
        3: 'z₁',
        4: 'P₂',
        5: 'V₂',
        6: 'z₂',
        7: 'hf',
        8: 'presión',
        9: 'densidad',
        10: 'aceleración de la gravedad',
        11: 'velocidad del fluido',
        12: 'altura sobre nivel referencia',
        13: 'pérdidas por fricción'
      },
      dropTargets: 13
    },
    {
      id: 'viscosidad-aparente',
      title: 'Cálculo de Viscosidad Aparente',
      description: 'Resuelve el problema arrastrando los valores y operaciones correctas a los espacios en blanco.',
      text: `Problema: Un fluido no newtoniano sigue la ley potencia con K = 3 Pa·s y n = 0.45. Calcular la viscosidad aparente (μₐₚ) cuando se somete a una tasa de deformación de γ = 10 s⁻¹.

Solución:
Utilizamos la ecuación: μₐₚ = [1] · γ^[2]

Datos:
- K = [3] Pa·s
- n = [4]
- γ = [5] s⁻¹

Desarrollo:
μₐₚ = [3] · [5]^([4]-1)
μₐₚ = 3 · 10^([6])
μₐₚ = 3 · [7]
μₐₚ = [8] Pa·s

Por tanto, la viscosidad aparente de este fluido a 10 s⁻¹ es [8] Pa·s, lo que demuestra su comportamiento [9] ya que la viscosidad [10] al aumentar la tasa de deformación.`,
      options: [
        'K', 'n-1', '3', '0.45', '10', '-0.55', '0.282', '0.846', 'pseudoplástico', 'disminuye',
        'aumenta', 'dilatante', 'n+1', '1.45', '0.55', '3.55', '1.78'
      ],
      answers: {
        1: 'K',
        2: 'n-1',
        3: '3',
        4: '0.45',
        5: '10',
        6: '-0.55',
        7: '0.282',
        8: '0.846',
        9: 'pseudoplástico',
        10: 'disminuye'
      },
      dropTargets: 10
    },
    {
      id: 'calculo-bomba',
      title: 'Cálculo de Potencia de Bomba',
      description: 'Resuelve el problema de bombeo arrastrando los valores adecuados para completar el cálculo.',
      text: `Problema: Una bomba con eficiencia del 60% eleva agua desde un tanque a nivel 0 m hasta una altura de 15 m. Si el diámetro de succión es 75 mm y el de descarga 50 mm, con un caudal de 0.5 L/s, calcular la potencia necesaria. Suponer pérdidas por fricción de 20 J/kg.

Solución:
Datos:
- Densidad del agua = [1] kg/m³
- Gravedad = [2] m/s²
- Eficiencia = [3] %
- Altura = [4] m
- Diámetro succión = [5] mm
- Diámetro descarga = [6] mm
- Caudal = [7] m³/s
- Pérdidas = [8] J/kg

Áreas:
- A₁ = π × (0.075)² / 4 = [9] m²
- A₂ = π × (0.050)² / 4 = [10] m²

Velocidades:
- V₁ = Q / A₁ = [11] m/s
- V₂ = Q / A₂ = [12] m/s

Trabajo de la bomba (sin eficiencia):
Wₚ = g × h + (V₂² - V₁²) / 2 + hf
Wₚ = [2] × [4] + ([12]² - [11]²) / 2 + [8]
Wₚ = [13] + [14] + [8]
Wₚ = [15] J/kg

Trabajo real considerando eficiencia:
Wₚ_real = Wₚ / (η/100) = [15] / ([3]/100) = [16] J/kg

Potencia de la bomba:
P = Wₚ_real × ρ × Q = [16] × [1] × [7] = [17] W = [18] kW`,
      options: [
        '1000', '9.81', '60', '15', '75', '50', '0.0005', '20', '0.00442', '0.00196',
        '0.113', '0.255', '147.15', '0.026', '167.18', '278.63', '139.32', '0.139'
      ],
      answers: {
        1: '1000',
        2: '9.81',
        3: '60',
        4: '15',
        5: '75',
        6: '50',
        7: '0.0005',
        8: '20',
        9: '0.00442',
        10: '0.00196',
        11: '0.113',
        12: '0.255',
        13: '147.15',
        14: '0.026',
        15: '167.18',
        16: '278.63',
        17: '139.32',
        18: '0.139'
      },
      dropTargets: 18
    }
  ];

  // Función para manejar el arrastre
  const handleDragStart = (e, option) => {
    e.dataTransfer.setData('text/plain', option);
  };

  // Función para permitir el drop
  const handleDragOver = (e) => {
    e.preventDefault();
  };

  // Función para manejar el drop
  const handleDrop = (e, targetId) => {
    e.preventDefault();
    const droppedText = e.dataTransfer.getData('text/plain');
    
    // Actualizar las respuestas del usuario
    setUserAnswers(prev => ({
      ...prev,
      [targetId]: droppedText
    }));
  };

  // Función para verificar las respuestas
  const checkAnswers = async () => {
    const currentAnswers = quizzes[currentQuiz].answers;
    let correct = true;
    
    // Verificar cada respuesta
    for (const key in currentAnswers) {
      if (userAnswers[key] !== currentAnswers[key]) {
        correct = false;
        break;
      }
    }
    
    if (correct) {
      setScore(prev => prev + 1);
      setAllCorrect(true);
      
      // Enviar actualización de progreso al backend si está disponible
      if (backendAvailable && attemptId) {
        try {
          await fetch(`${API_BASE_URL}/api/quiz-attempts/${attemptId}/progress`, {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
            },
            body: JSON.stringify({
              questionId: quizzes[currentQuiz].id,
              correct: true,
              currentScore: score + 1,
              timeTaken
            }),
          });
        } catch (err) {
          console.error('Error al actualizar progreso:', err);
        }
      }
    }
    
    setShowFeedback(true);
  };

  // Función para pasar al siguiente quiz
  const nextQuiz = () => {
    if (currentQuiz < quizzes.length - 1) {
      setCurrentQuiz(prev => prev + 1);
    } else {
      setIsCompleted(true);
      // Enviar resultados finales cuando se completa el quiz
      sendResults();
      
      // Detener el temporizador
      if (timerRef.current) {
        clearInterval(timerRef.current);
      }
    }
  };

  // Función para reiniciar el quiz
  const resetQuiz = () => {
    setCurrentQuiz(0);
    setScore(0);
    setIsCompleted(false);
    setUserAnswers({});
    setShowFeedback(false);
    setAllCorrect(false);
    setQuizStarted(false);
    setStudentInfo(null);
    setAttemptId(null);
    setTimeTaken(0);
    setError('');
  };

  // Función para formatear el tiempo
  const formatTime = (seconds) => {
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`;
  };

  // Función para renderizar el texto con espacios para drop
  const renderQuizText = () => {
    const quiz = quizzes[currentQuiz];
    const parts = quiz.text.split(/\[(\d+)\]/g);
    
    return parts.map((part, index) => {
      // Si es un número entre corchetes, renderizar drop target
      if (index % 2 === 1) {
        const targetId = part;
        const userAnswer = userAnswers[targetId];
        const isCorrect = showFeedback && (userAnswer === quiz.answers[targetId]);
        const isIncorrect = showFeedback && userAnswer && !isCorrect;

        return (
          <span 
            key={index}
            className={`inline-block px-2 py-1 mx-1 border-2 rounded-md min-w-[120px] min-h-[36px] text-center
              ${userAnswer ? 'border-blue-500 bg-blue-50' : 'border-dashed border-gray-400'} 
              ${isCorrect ? 'border-green-500 bg-green-100' : ''}
              ${isIncorrect ? 'border-red-500 bg-red-100' : ''}`}
            onDragOver={handleDragOver}
            onDrop={(e) => handleDrop(e, targetId)}
            ref={(el) => {
              if (el && !dropTargets.current.includes(el)) {
                dropTargets.current.push(el);
              }
            }}
          >
            {userAnswer || ''}
            {showFeedback && !isCorrect && (
              <span className="block text-xs text-red-500">
                Correcto: {quiz.answers[targetId]}
              </span>
            )}
          </span>
        );
      }
      
      // Si es texto normal, renderizarlo como está
      return <span key={index}>{part}</span>;
    });
  };

  // Si el usuario no ha iniciado sesión, mostrar la pantalla de login
  if (!quizStarted) {
    return <QuizLogin onLogin={handleLogin} />;
  }

  // Si el quiz está completado, mostrar la puntuación final
  if (isCompleted) {
    return (
      <div className="bg-white rounded-lg shadow-md p-8 text-center">
        <h2 className="text-2xl font-bold text-blue-800 mb-4">¡Quiz Completado!</h2>
        
        <div className="flex items-center justify-center mb-6">
          <span className="text-4xl mr-3">{studentInfo.avatar}</span>
          <span className="text-xl font-medium text-gray-700">{studentInfo.name}</span>
        </div>
        
        <p className="text-lg mb-2">Tu puntuación final es: <span className="font-bold">{score}/{quizzes.length}</span></p>
        <p className="text-sm text-gray-600 mb-4">Tiempo total: {formatTime(timeTaken)}</p>
        
        <div className="h-4 bg-gray-200 rounded-full overflow-hidden mb-4">
          <div 
            className="h-full bg-blue-600 rounded-full"
            style={{ width: `${(score / quizzes.length) * 100}%` }}
          ></div>
        </div>
        
        {!backendAvailable ? (
          <p className="text-yellow-600 mb-6">El servidor no está disponible. Tus resultados se han guardado localmente.</p>
        ) : loading ? (
          <p className="text-blue-600 mb-6">Enviando resultados...</p>
        ) : error ? (
          <p className="text-yellow-600 mb-6">Tu resultado se ha guardado localmente. {error}</p>
        ) : (
          <p className="text-green-600 mb-6">Tu resultado ha sido enviado correctamente.</p>
        )}
        
        {score === quizzes.length ? (
          <p className="text-green-600 mb-6">¡Felicidades! Has respondido correctamente todos los ejercicios.</p>
        ) : (
          <p className="text-blue-600 mb-6">Puedes intentarlo de nuevo para mejorar tu puntuación.</p>
        )}
        
        <button
          className="px-6 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 transition-colors"
          onClick={resetQuiz}
        >
          Reiniciar Quiz
        </button>
      </div>
    );
  }

  // Renderizar el quiz actual
  const quiz = quizzes[currentQuiz];

  return (
    <div className="bg-white rounded-lg shadow-md p-6">
      {/* Información del estudiante y temporizador */}
      <div className="flex justify-between items-center mb-4">
        <div className="flex items-center">
          <span className="text-xl mr-2">{studentInfo.avatar}</span>
          <span className="font-medium">{studentInfo.name}</span>
        </div>
        <div className="bg-gray-100 px-3 py-1 rounded-md text-gray-700">
          <span className="font-medium">{formatTime(timeTaken)}</span>
        </div>
      </div>
      
      {/* Barra de progreso */}
      <div className="mb-4">
        <div className="flex justify-between items-center mb-2">
          <span className="text-sm text-gray-600">Progreso</span>
          <span className="text-sm font-medium text-blue-600">{currentQuiz + 1}/{quizzes.length}</span>
        </div>
        <div className="h-2 bg-gray-200 rounded-full overflow-hidden">
          <div 
            className="h-full bg-blue-600 rounded-full transition-all duration-300"
            style={{ width: `${((currentQuiz + 1) / quizzes.length) * 100}%` }}
          ></div>
        </div>
      </div>

      {/* Título y descripción */}
      <h2 className="text-xl font-semibold text-blue-800 mb-2">{quiz.title}</h2>
      <p className="text-gray-600 mb-6">{quiz.description}</p>

      {/* Texto del quiz */}
      <div className="mb-8 p-4 bg-gray-50 rounded-lg whitespace-pre-line leading-relaxed">
        {renderQuizText()}
      </div>

      {/* Opciones para arrastrar */}
      <div className="mb-6">
        <h3 className="text-md font-medium text-gray-700 mb-2">Arrastra los términos correctos:</h3>
        <div className="flex flex-wrap gap-2">
          {quiz.options.map((option, index) => (
            <div
              key={index}
              className={`px-3 py-1.5 bg-blue-100 text-blue-800 rounded-full cursor-move
                ${Object.values(userAnswers).includes(option) ? 'opacity-50' : 'hover:bg-blue-200'}`}
              draggable={!Object.values(userAnswers).includes(option)}
              onDragStart={(e) => handleDragStart(e, option)}
            >
              {option}
            </div>
          ))}
        </div>
      </div>

      {/* Feedback */}
      {showFeedback && (
        <div className={`p-4 mb-6 rounded-md ${allCorrect ? 'bg-green-100' : 'bg-red-100'}`}>
          <p className={`font-medium ${allCorrect ? 'text-green-700' : 'text-red-700'}`}>
            {allCorrect 
              ? '¡Respuestas correctas! Puedes continuar al siguiente ejercicio.' 
              : 'Algunas respuestas son incorrectas. Las respuestas correctas se muestran en el texto.'}
          </p>
        </div>
      )}

      {/* Botones de control */}
      <div className="flex justify-between">
        <div>
          {currentQuiz > 0 && (
            <button
              className="px-4 py-2 border border-gray-300 rounded-md mr-2 hover:bg-gray-50"
              onClick={() => setCurrentQuiz(prev => prev - 1)}
            >
              Anterior
            </button>
          )}
        </div>
        <div>
          {!showFeedback ? (
            <button
              className="px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 disabled:opacity-50"
              onClick={checkAnswers}
              disabled={Object.keys(userAnswers).length !== quiz.dropTargets}
            >
              Verificar Respuestas
            </button>
          ) : (
            <button
              className="px-4 py-2 bg-green-600 text-white rounded-md hover:bg-green-700"
              onClick={nextQuiz}
            >
              {currentQuiz < quizzes.length - 1 ? 'Siguiente' : 'Ver Resultados'}
            </button>
          )}
        </div>
      </div>
    </div>
  );
};

export default FluidMechanicsQuiz;